import React, {useState} from 'react';

function LandingPage(){
  const [isOpen, setIsOpen] = useState(false)

  const toggle = ()=>{
      setIsOpen(!isOpen);
  }
    return(
       <div className="content">
          <div className="header">
            <nav>
              <div className="logo">
                <img src="/images/logo.png"/>
              </div>
              <div className="social_media">
                <a href="https://www.facebook.com"><i className="fa fa-facebook"></i></a>
                <i className="fa fa-twitter"></i>
                <i className="fa fa-youtube"></i>
              </div>
              <ul className={isOpen ? "":"hidden"}>
                <li className="active"><a href="#home">HOME</a></li>
                <li><a href="#products">PRODUCTS</a></li>
                <li><a href="#about">ABOUT</a></li>
              </ul>
              <i className="fa fa-bars burger" onClick={toggle}></i>
            </nav>
          </div>
          <div className="sections">
            <section id="home" style={{backgroundImage : `url("/images/carbonara1.jpg")`}}>
              <div className="overlay_box">&nbsp;</div>
              <div className="card">
                <div className="title">
                  <img src="/images/logo.png"/>
                </div>
                <h1>JMFV DiNER</h1>
                <span>Good food and greate vibes.</span>
                <div className="contact_information">
                  <i className="fa fa-calendar"><label>Monday - Sunday</label></i>
                  <i className="fa fa-globe"><label>(8:00am - 10:00pm)</label></i>
                  <i className="fa fa-phone"><label>+639-55-50-93-254</label></i>
                  <i className="fa fa-map-marker"><label>Banday, Tomas Oppus, Southern Leyte</label></i>
                </div>
              </div>
            </section>
            <section id="products">

            </section>
            <section id="about">

            </section>
          </div>
       </div>
    )
}

export default LandingPage;